import React, { Component, useEffect } from "react";
import { Container } from "@material-ui/core";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import { makeStyles } from "@material-ui/core/styles";
import { useParams, withRouter } from "react-router-dom";
import { Link } from "react-router-dom";
import Footer from "../Footer/Footer";
import "./Forum.css";
import CustomLoader from "../Loader";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    backgroundColor: theme.palette.background.paper,
  },
  hover: {
    "&:hover": {
      backgroundColor: "#f7f7f7",
    },
  },
}));

function ModuleList({ course }) {
  const classes = useStyles();
  return course.modules.map((module) => {
    return (
      <Link
        to={`/forum/${course.slug}/1/${module.slug}/${module.id}`}
        style={{ textDecoration: "none", color: "black" }}
      >
        <ListItem className={classes.hover}>
          <ListItemText
            primary={module.title}
            secondary={`Discuss and ask questions about ${module.title}`}
          />
        </ListItem>
      </Link>
    );
  });
}

class ForumComponent extends Component {
  render() {
    return (
      <>
        <Container className="forum" fixed style={{ marginBottom: 40 }}>
          <div>
            <h2>Discussion Forum for {this.props.course.title}</h2>
            <hr />
            <List>
              <ModuleList course={this.props.course} />
            </List>
          </div>
        </Container>
        <Footer />
      </>
    );
  }
}

function Forum(props) {
  let { course } = useParams();

  const success = () => {
    console.log("success");
  };

  const failed = () => {
    props.history.push("/home");
  };

  useEffect(() => {
    if (
      Object.keys(props.courses.courseData).length === 0 &&
      props.courses.courseData.constructor === Object
    ) {
      props.fetchCourseData(course, success, failed);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (
    !(
      Object.keys(props.courses.courseData).length === 0 &&
      props.courses.courseData.constructor === Object
    )
  ) {
    if (
      Object.keys(props.courses.courseData).length > 0 &&
      props.courses.courseData.constructor === Object
    ) {
      console.log(props.courses.courseData);
      return <ForumComponent course={props.courses.courseData} />;
    }
  } else {
    return (
      <div className="loader">
        <CustomLoader />
      </div>
    );
  }
}

export default withRouter(Forum);
